import React from "react";
import styled from "styled-components";
import { getCount } from "../../apis/utils";
import style from "../../assets/style";
export const ListWrapper = styled.div`
  max-width: 100%;
  .title {
    font-weight: 700;
    padding-left: 6px;
    font-size: 14px;
    line-height: 60px;
  }
`;
export const List = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
`;

export const ListItem = styled.div`
  position: relative;
  width: 32%;

  .image-wrapper {
    .decorate {
      position: absolute;
      top: 0;
      width: 100%;
      height: 35px;
      border-radius: 3px;
      background: linear-gradient(hsla(0, 0%, 43%, 0.4), hsla(0, 0%, 100%, 0));
    }
    position: relative;
    height: 0;
    padding-bottom: 100%;
    .play-count {
      position: absolute;
      right: 2px;
      top: 2px;
      font-size: ${style["font-size-s"]};
      line-height: 15px;
      color: ${style["font-color-light"]};
      .play {
        vertical-align: top;
      }
    }
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 3px;
    }
  }
  .desc {
    overflow: hidden;
    margin-top: 2px;
    padding: 0 2px;
    height: 50px;
    text-align: left;
    font-size: ${style["font-size-s"]};
    line-height: 1.4;
    color: ${style["font-color-desc"]};
  }
`;

export const RecommendList = React.memo(({ recommendList }) => {
  return (
    <ListWrapper>
      <h1>推荐歌单</h1>
      <List>
        {recommendList.map((recommend) => {
          return (
            <ListItem key={recommend.id}>
              <div className="image-wrapper">
                <div className="decorate"></div>
                <img
                  src={recommend.imageUrl}
                  alt="推荐"
                  height="100%"
                  width="100%"
                />
                <div className="play-count">
                  <i className="iconfont play">&#xe885;</i>
                  <span className="count">{getCount(recommend.playCount)}</span>
                </div>
                <div className="desc">{recommend.name}</div>
              </div>
            </ListItem>
          );
        })}
      </List>
    </ListWrapper>
  );
});
